.main-comp-wrapper{border:8px skyblue solid;width:80vw;min-width:600px;height:93vh;margin:0 auto;font-family:sans-serif}canvas{border:blue 2px solid;margin:0 auto}.nav-modals{display:flex;justify-content:space-evenly;border:2px solid steelblue}.board-container{width:600px;margin:0 auto}.board-container #gameCanvas{border:1px solid green;margin:0 auto}.instructions-disp{display:none}.instructions-modal{color:mediumslateblue}.instructions-modal:hover>.instructions-disp{position:absolute;display:block;width:20%;background-color:orange;z-index:5;padding:10px;border-radius:5px}.hi{color:red}.td-tittle{color:blueviolet;text-align:center;font-family:cursive;font-weight:600;font-size:30px}.player-container{margin-top:20px;width:100%}#money-container,#health-container{width:50%;text-align:center;color:blueviolet;font-size:25px;font-weight:600}.footer{width:50%;margin:0 auto;background:lightblue;padding:15px 5px;border:2px solid cornflowerblue;border-radius:8px;font-family:sans-serif;display:flex;justify-content:space-evenly}.restart-btn{width:auto;padding:5px;position:relative;border:2px solid blue;display:inline-block;bottom:50px;left:50vw;background-color:lightblue;font-size:25px;font-weight:600}.restart-btn:hover{background-color:skyblue;cursor:pointer}.lose-modal{position:fixed;left:0;top:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5)}.hide{display:none}.modal-content{width:18%;height:18%;margin:0 auto;margin-top:14%;padding:100px;border:skyblue solid 8px;background-color:lightblue;text-align:center}.lose-text{color:blueviolet;font-size:25px;font-weight:600}.modal-restart-btn{width:auto;padding:5px;border:2px solid blue;background-color:skyblue;font-size:25px;font-weight:600;margin-top:25px}.modal-restart-btn:hover{background-color:#35afd8;cursor:pointer}
